body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #e89c05;
  }
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #e89c05;
  }
}



